@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap');
*{
  margin: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nunito Sans', sans-serif;
}
.navbar{
  background-color: #fff !important;
  & .navbar-brand{
    font-size: 1.9rem !important;
    color: #565387 !important;
  }
  & a{
    font-size: 1.3rem ;
    text-transform: capitalize;
  }
  & .menu_active{
    font-weight: bold;
    border-bottom: 1px solid #565387 !important;
  } 
  & a:hover{
    color: #3498db !important;
  } 
}
@media (max-width:991px){
  .navbar-brand{
    font-size: 1.5rem !important;
  }
  .navbar .menu-active{
    font-weight: bold;
    border-bottom: none;
  }
}
/*=====================================================================
#main landing Section
==========================================================*/
#header{
  width: 100%;
  height: 75vh;
  margin-top: 70px;
  & h1{
    max-width: 0 0 10px 0;
    font-size: 48px;
    font-weight: 700;
    color: black;
  }
  & .brand-name{
    color: #3498db;
  }
  & h2{
    color: #484848;
    font-size: 24px;
  }
  & .btn-get-started{
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
  }
  & .btn-get-started:hover{
    background: #3498db;
    color: #fff;
  }
  & header-img{
    text-align: right;
  }
  & .animated{
    animation: up-down 3s ease-in-out infinite alternate-reverse both ;
  }
}
@-webkit-keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
@keyframes up-down{
  0%{
    transform: translateY(10px);
  }
  100%{
    transform: translateY(-10px);
  }
}
/*==============================================================
contact div css
==================================*/
.contact_div{
  width: 100%;
  height: 65vh;
}
/*==============================================================
footer css
==================================*/
footer p{
  padding: 0;
  margin: 0;
  background-color: palegreen;
}
@media (max-width:991px){
  #header{
    height: 80vh;
    text-align: center;
    & .header-img{
      text-align: center;
    }
    & .header-img img{
      width: 60%;
    }
  }
}
@media (max-width:768px){
  #header{
    margin-top: 20px;
    & h1{
      font-size: 28px;
    }
    & h2{
      font-size: 18px;
      margin-bottom: 30px;
    }
    & .header-img img{
      width: 70%;
    }
  }
}
