@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans&display=swap);
*{margin:0;margin:0;padding:0;box-sizing:border-box;font-family:'Nunito Sans', sans-serif}.navbar{background-color:#fff !important}.navbar .navbar-brand{font-size:1.9rem !important;color:#565387 !important}.navbar a{font-size:1.3rem;text-transform:capitalize}.navbar .menu_active{font-weight:bold;border-bottom:1px solid #565387 !important}.navbar a:hover{color:#3498db !important}@media (max-width: 991px){.navbar-brand{font-size:1.5rem !important}.navbar .menu-active{font-weight:bold;border-bottom:none}}#header{width:100%;height:75vh;margin-top:70px}#header h1{max-width:0 0 10px 0;font-size:48px;font-weight:700;color:black}#header .brand-name{color:#3498db}#header h2{color:#484848;font-size:24px}#header .btn-get-started{font-weight:500;font-size:16px;letter-spacing:1px;display:inline-block;padding:10px 30px;border-radius:50px;transition:0.5s;color:#3498db;border:2px solid #3498db;text-decoration:none}#header .btn-get-started:hover{background:#3498db;color:#fff}#header header-img{text-align:right}#header .animated{animation:up-down 3s ease-in-out infinite alternate-reverse both}@-webkit-keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}@keyframes up-down{0%{-webkit-transform:translateY(10px);transform:translateY(10px)}100%{-webkit-transform:translateY(-10px);transform:translateY(-10px)}}.contact_div{width:100%;height:65vh}footer p{padding:0;margin:0;background-color:palegreen}@media (max-width: 991px){#header{height:80vh;text-align:center}#header .header-img{text-align:center}#header .header-img img{width:60%}}@media (max-width: 768px){#header{margin-top:20px}#header h1{font-size:28px}#header h2{font-size:18px;margin-bottom:30px}#header .header-img img{width:70%}}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

